import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AuthService } from '@core/auth/auth.service';
import { Observable } from 'rxjs';
import { IUsuarioResponse } from '../models/usuario';
import { environment } from 'environments/environment';

@Injectable()
export class UsuariosService {
    constructor(
        private http: HttpClient, 
        private _authService: AuthService
    ) {}

    /**
     * 
     * @param usuarios
     * @returns Genera un reporte de etiquetas
     */
    credencialUsuario(usuarios: { idusuario: number }[]) {
        return this.http.post<IUsuarioResponse>(`${environment.login.credencial_usuario}`, usuarios);
    }
}